* {
  margin: 0;
  padding: 0;
  font-family: arial;
  font-size: 16px;
  box-sizing: border-box;
}

body {
  min-width: 1080px;
}

#App {
  width: 100%;
  max-width: 1440px;
  min-height: 100vh;
  background: #ffffff;
}

.flex {
  display: flex;
  width: 100%;
}

.flex-col {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.text-center {
  text-align: center;
}

/* FONT STYLES */
.font-xs {
  font-size: 0.75rem;
}

.font-sm {
  font-size: 1.25rem;
}

.font-m {
  font-size: 1.5rem;
}

.font-l {
  font-size: 2rem;
}

.font-xl {
  font-size: 2.5rem;
}

.font-xxl {
  font-size: 3rem;
}

.font-primary {
  color: #000000;
}

.font-secondary {
  color: #5f5f5f;
}

.font-tertiary {
  color: #1262ec;
}

.font-red {
  color: #ff0000;
}

.font-green {
  color: #00873c;
}

.no-text-decoration {
  text-decoration: none;
}

.no-text-decoration:hover {
  text-decoration: none;
}

.react-icons {
  vertical-align: middle;
}
